<template>
  <!--  -->
  <div>
    <div class="broadcast" @touchstart="hideJubao">
      <!-- <div class="header clearfix">
        <span class="pull-left">App,</span>
        <div class="pull-right">
          <button class="download"></button>
          <button class="open"></button>
        </div>
      </div> -->
      <div class="login">
        <img src="../assets/picture/person.jpg" alt=""/>
        <span @click="isLayout=true;isShow=true;"></span>
      </div>
      <div v-for="item in arr" :key="item" class="comment clearfix">
        <img src="../assets/picture/download.png" class="pull-left" alt=""/>
        <div class="comment-content pull-right">
          <h2><strong></strong></h2>
          <h3>2017-09-06 15:42:43</h3>
          <p></p>
          <img src="../assets/image/20171204184128.jpg" alt=""/>
          <div class="comment-foot clearfix">
            <div @click="isLayout=true;isShow=true;" class="zan pull-left">
              <img src="../assets/picture/zan4.png" alt=""/>
              <span>233</span>
            </div>
            <div class="jubao pull-right">
              <span @click="showJubao($event)">. . .</span>
              <div class="jubao-box" @click="isLayout=true;isShow=true;"></div>
            </div>
          </div>
        </div>
      </div>
      <footer>
        <douban-footer></douban-footer>
      </footer>
    </div>   
    <!--  -->
    <lay-out :isLayout="isLayout" :state="state" @close-layout="close">
      <div :slot="state" class="login-layer">
          <h2></h2>
          <router-link to="/login" tag="button"></router-link>
          <!-- <input type="button" value=" App"/> -->
          <router-link to="/register" tag="button"></router-link>
      </div>
    </lay-out>
    <transition name="mask">
      <mask-layer v-show="isShow" @click.native="isLayout=false;isShow=false"></mask-layer>
    </transition>
  </div>
</template>

<script>
import doubanFooter from "./footer"
import layout from "../components/layout"
import maskLayer from "../components/maskLayer"
export default {
  data: function(){
    return {
      arr: [1,2,3,4,5,6],
      isLayout: false,
      isShow: false,
      state: 'login'
    }
  },
  components: {
    'douban-footer': doubanFooter,
    'lay-out': layout,
    'mask-layer': maskLayer
  },
  mounted: function(){
    this.$common.scrollToTop();
  },
  methods: {
    getImage: function(url){
      if(url !== undefined){
          return url.replace(/^http\w{0,1}:\/\//ig,'https://images.weserv.nl/?url=');
      }
    },   
    close: function(pra){
      this.isLayout = pra;
      this.isShow = pra;
    },
    showJubao: function(event){
      var obj = event.currentTarget.nextSibling;
      var self = obj.nodeType == 3?obj.nextSibling:obj;
      self.style.display = 'block';
    },
    hideJubao: function(e){
      var obj = e.target;
      if(obj.className != 'jubao-box'){
        var arr = document.getElementsByClassName('jubao-box');
        var length = arr.length;
        for(let i = 0;i<length;i++){
          arr[i].style.display='none';
        }
      }
    }
  }
}
</script>

<style scoped>
/*.header {
  height: 5rem;
  line-height: 5rem;
}

.header span {
  color:#42bd56;
  font-size: 1.3rem;
}

.header .download {
  padding:5px 20px;
  background-color: #42bd56;
  color:#fff;
  border-radius: 5px;
  font-weight: bold;
  font-size: 1.3rem;
}

.header .open {
  padding:5px 15px;
  color: #42bd56;
  border:1px solid #42bd56;
  border-radius: 5px;
  background-color: #fff;
  font-weight: bold;
  font-size: 1.3rem;
}
*/
.login {
  padding:15px 0;
  border-bottom: 1px solid #ddd;
}

.login img {
  width: 4rem;
  border-radius: 4rem;
  vertical-align: middle;
}

.login span {
  font-size: 1.3rem;
  vertical-align: middle;
  margin-left: 10px;
  color:#aaa;
}

.comment {
  margin-top: 20px;
}

.comment>img {
  width: 13%;
  border-radius: 100%;
}

.comment-content {
  width: 84%;
}

.comment-content h2 {
  font-size: 1.4rem;
  color:#aaa;
}

.comment-content h2 strong {
  color:#333;
  margin-right: 5px;
}

.comment-content h3 {
  padding:5px 0 10px 0;
  font-size: 1.2rem;
  color:#aaa;
}

.comment-content p {
  font-size: 1.4rem;
}

.comment-content>img {
  display: block;
  width: 100%;
  margin:15px 0 20px 0;
}

.comment-foot {
  font-size: 1.2rem;
  color:#aaa;
}

.comment-foot .zan img {
  width: 1.8rem;
  vertical-align: middle;
}

.comment-foot .zan span {
  vertical-align: middle;
  margin-left: 5px;
}

.comment-foot .jubao {
  position: relative;
}

.comment-foot .jubao span{
  font-weight: bolder;
}

.comment-foot .jubao .jubao-box {
  display: none;
  position: absolute;
  right:-10px;
  top:-5.5rem;
  width: 9rem;
  height: 4rem;
  line-height: 4rem;
  font-size: 1.4rem;
  color: #42bd56;
  border:1px solid #ddd;
  box-shadow: 0 0 1px #ddd;
  text-align: center;
  background: #fff;
  border-radius: 5px;
}

.login-layer {
  text-align: center;
}

.login-layer h2 {
  font-size: 1.5rem;
  padding-bottom: 25px;
}

.login-layer button,.login-layer input {
  width: 100%;
  padding:5px 0;
  font-size: 1.3rem;
  border-radius: 5px;
}

.login-layer button {
  color:#fff;
  background-color:#24be2f;
  margin-bottom: 10px;
}

.login-layer input {
  background-color: #fff;
  color:#24be2f;
  border:1px solid #24be2f;
  margin-bottom: 15px;
}

.login-layer span {
  font-size: 1.3rem;
  color:#24be2f;
}

.mask-enter-active,.mask-leave-active {
  transition: opacity .3s ease;
}

.mask-leave-active {
  transition: opacity .1s linear .5s;
}

.mask-enter,.mask-leave-active {
  opacity: 0;
}

</style>



