<template>
	<div class="share clearfix">
		<a :href="wxURL">
			<img src="../assets/picture/wx.png" alt=""/>
		</a>
		<a :href="qqURL">
			<img src="../assets/picture/qq.png" alt=""/>
		</a>
		<a :href="wbURL">
			<img src="../assets/picture/wb.png" alt=""/>
		</a>
		<a :href="qzoneURL">
			<img src="../assets/picture/qzone.png" alt=""/>
		</a>
		{{changeURL}}
	</div>
</template>

<script>
export default {
  props: ['socail'],
  data: function(){
    return {  
    	wxURL: '',
    	qqURL: '',
    	wbURL: '',
    	qzoneURL: ''
    }
  },
  computed: {
  	changeURL: function(){
  		this.wbURL = "http://v.t.sina.com.cn/share/share.php?title="+this.socail.title+"&url="+this.socail.url;
  		this.qqURL = "http://v.t.qq.com/share/share.php?title="+this.socail.title+"&url="+this.socail.url;
  		this.qzoneURL = "http://sns.qzone.qq.com/cgi-bin/qzshare/cgi_qzshare_onekey?url="+this.socail.title+"&url="+this.socail.url;
  	}
  }
}
</script>

<style scoped>
.share a {
	float: left;
	width: 2.5rem;
	height: 2.5rem;
}

.share a + a {
	margin-left: 1rem;
}

.share a img {
	display: block;
	width: 100%;
}

</style>



