import Mock from 'mockjs'

//  mock.Random 
const Random = Mock.Random;
// mock
const produceNewsData = function() {
    let dataList = [];
    for (let i = 0; i < 50; i++) {
        let newArticleObject = {
            contentTitle: Random.ctitle(4,15), //  Random.csentence( min, max )
            contentPic: Random.dataImage('120x240', 'Mock.js'), // Random.dataImage( size, text )  Base64 
            contentTxt: Random.cparagraph(2,3), // Random.cname() 
            contentTime: Random.datetime() // Random.date(),yyyy-MM-ddRandom.time() 
        }
        dataList.push(newArticleObject)
    }
 
    return {
        dataList: dataList
    }
}

Mock.mock('/news/index', 'get', produceNewsData);


