<template>
  <!--  -->
  <div>
    <div class="group">
      <div class="login">
        <img src="../assets/picture/person.jpg" alt=""/>
        <span @click="isLayout=true;isShow=true;"></span>
      </div>
      <div class="group-content">
        <div v-for="(key,index) in count" :key="index" class="group-detail">
          <h2>{{key.title}}</h2>
          <div @click="isLayout=true;isShow=true;" v-for="item in key.type" :key="item.number" class="group-box">
            <div class="clearfix">
              <div class="pull-left">
                <img :src="item.img" alt=""/>
                <span class="nowrap">{{item.title}}</span>
              </div>
              <h3 class="pull-right">{{item.number}}</h3>
            </div>
            <p class="nowrap">{{item.info}}</p>
          </div>
          <h4 @click="isLayout=true;isShow=true;">{{key.more}}</h4>
        </div>
      </div>
    </div>   
    <footer>
      <douban-footer></douban-footer>
    </footer>
    <!--  -->
    <lay-out :isLayout="isLayout" :state="state" @close-layout="close">
      <div :slot="state" class="login-layer">
          <h2></h2>
          <router-link to="/login" tag="button"></router-link>
          <!-- <input type="button" value=" App"/> -->
          <router-link to="/register" tag="button"></router-link>
      </div>
    </lay-out>
    <transition name="mask">
      <mask-layer v-show="isShow" @click.native="isLayout=false;isShow=false"></mask-layer>
    </transition>
  </div>
</template>

<script>
import doubanFooter from "./footer"
import layout from "../components/layout"
import maskLayer from "../components/maskLayer"
export default {
  data: function(){
    return {
      isLayout: false,
      isShow: false,
      state: 'login',
      count: [
        {
          title: '',
          more: '',
          type: [
            {
              title: '',
              img: require('../assets/picture/g117205-2.jpg'),
              info:'3 ',
              number: 1230
            },
            {
              title: '',
              img: require('../assets/picture/g279962-3.jpg'),
              info:'9',
              number: 4234
            },
            {
              title: '',
              img: require('../assets/picture/g117205-2.jpg'),
              info:'2800',
              number: 23324
            }
          ],
        },
        {
          title: '',
          more: '',
          type: [
            {
              title: '',
              img: require('../assets/picture/g14859-17.jpg'),
              info:'',
              number: 4224
            },
            {
              title: '',
              img: require('../assets/picture/g17947-8.jpg'),
              info:'1',
              number: 234
            },
            {
              title: '',
              img: require('../assets/picture/g17947-8.jpg'),
              info:'3',
              number: 3452
            }
          ],
        },
        {
          title: '',
          more: '',
          type: [
            {
              title: '&amp;',
              img: require('../assets/picture/g573696-2.jpg'),
              info:'4 +   ',
              number: 3535
            },
            {
              title: 'PiaPia',
              img: require('../assets/picture/g25704-22.jpg'),
              info:'1',
              number: 54546
            },
            {
              title: '',
              img: require('../assets/picture/g573696-2.jpg'),
              info:'3 +  ',
              number: 4546
            }
          ],
        },
      ]
    }
  },
  components: {
    'douban-footer': doubanFooter,
    'lay-out': layout,
    'mask-layer': maskLayer
  },
  mounted: function(){
    this.$common.scrollToTop();
  },
  methods: {  
    close: function(pra){
      this.isLayout = pra;
      this.isShow = pra;
    }
  }
}
</script>

<style scoped>
.login {
  padding:15px 0;
  border-bottom: 1px solid #ddd;
}

.login img {
  width: 4rem;
  border-radius: 4rem;
  vertical-align: middle;
}

.login span {
  font-size: 1.3rem;
  vertical-align: middle;
  margin-left: 10px;
  color:#aaa;
}

.group-content {
  margin-top: 20px;
}

.group-detail h2 {
  font-size: 1.5rem;
}

.group-detail h4 {
  text-align: center;
  color:#42bd56;
  font-size: 1.3rem;
  padding-top: 20px;
}

.group-detail+.group-detail {
  margin-top: 60px;
}

.group-box {
  padding: 15px 0;
  border-bottom: 1px solid #ddd;
}

.group-box>div {
  height: 48px;
}

.group-box .pull-left {
  width: 75%;
}

.group-box .pull-right {
  width: 20%;
}

.group-box img {
  height: 48px;
  vertical-align: middle;
}

.group-box span {
  display: inline-block;
  width: 70%;
  font-size: 1.4rem;
  vertical-align: middle;
  margin-left: 10px;
}

.group-box h3 {
  font-size: 1.3rem;
  color:#aaa;
  line-height: 48px;
  text-align: right;
}

.group-box p {
  font-size: 1.3rem;
  color:#aaa;
  margin-top: 10px;
}

.login-layer {
  text-align: center;
}

.login-layer h2 {
  font-size: 1.5rem;
  padding-bottom: 25px;
}

.login-layer button,.login-layer input {
  width: 100%;
  padding:5px 0;
  font-size: 1.3rem;
  border-radius: 5px;
}

.login-layer button {
  color:#fff;
  background-color:#24be2f;
  margin-bottom: 10px;
}

.login-layer input {
  background-color: #fff;
  color:#24be2f;
  border:1px solid #24be2f;
  margin-bottom: 15px;
}

.login-layer span {
  font-size: 1.3rem;
  color:#24be2f;
}

.mask-enter-active,.mask-leave-active {
  transition: opacity .3s ease;
}

.mask-leave-active {
  transition: opacity .1s linear .4s;
}

.mask-enter,.mask-leave-active {
  opacity: 0;
}
</style>



