<template>
  <div class="search">
    <div class="search-content">
      <span @click="back"></span>
      <form action="" @submit.prevent>
        <input @search="toggle" v-model="headerVal" type="search">
      </form>
    </div>
    <div class="search-list clearfix">
      <ul class="pull-left">
        <li>
          <h2 style="color:#2384e8"></h2>
          <p></p>
        </li>
        <li>
          <h2 style="color:#e6467e"></h2>
          <p></p>
        </li>
        <li>
          <h2 style="color:#9f7860"></h2>
          <p></p>
        </li>
        <li>
          <h2 style="color:#e1644d"></h2>
          <p></p>
        </li>
      </ul>
      <ul class="pull-left">
        <li>
          <h2 style="color:#7a6adb"></h2>
          <p></p>
        </li>
        <li>
          <h2 style="color:#2ab8cc"></h2>
          <p></p>
        </li>
        <li>
          <h2 style="color:#5774c5"></h2>
          <p></p>
        </li>
        <li>
          <h2 style="color:#40cfa9">FM</h2>
          <p></p>
        </li>
      </ul>
      <ul class="pull-left">
        <li>
          <h2 style="color:#9f7860"></h2>
          <p></p>
        </li>
        <li>
          <h2 style="color:#f48f2e"></h2>
          <p></p>
        </li>
        <li>
          <h2 style="color:#596cdd"></h2>
          <p></p>
        </li>
        <li>
          <h2 style="color:#42bd56"></h2>
          <p></p>
        </li>
      </ul>
    </div>
    <div class="search-login clearfix">
      <ul class="pull-left">
        <router-link tag="li" to="/register"></router-link>
      </ul>
      <ul class="pull-right">
        <router-link tag="li" to="/login"></router-link>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  data: function(){
    return {
      headerVal: ''
    }
  },
  methods: {
    toggle: function(){
      let headerVal = this.headerVal;
      localStorage.getParam = headerVal;
      this.$router.push('/get');
    },
    back: function(){
      this.$router.go(-1);
    }
  }
}
</script>

<style scoped>
.search .search-content {
  padding:10px 0;
  text-align: center;
  border-bottom: 1px solid #f3f3f3;
}

.search .search-content span {
  vertical-align: middle;
  margin-right: 10px;
  color:#42bd56;
  font-size: 1.4rem;
}

.search .search-content form {
  width:80%;
  display: inline-block;
  border-radius: 5px;
  padding:8px 10px;
  background: #f3f3f3;
  vertical-align: middle;
}

.search .search-content form input {
  width:100%;
  background: #f3f3f3;
  font-size: 1.4rem;
}

.search .search-list ul {
  width: 33.33%;
  text-align: center;
}

.search .search-list ul li {
  margin-top: 20px;
}

.search .search-list ul h2 {
  font-size: 1.8rem;
}

.search .search-list ul p {
  font-size: 1.2rem;
  color:#999;
}

.search .search-login ul {
  width: 50%;
  text-align: center;
  margin-top:30px;
}

.search .search-login ul li {
  font-size: 1.4rem;
  color:#42bd56;
  padding:10px 0;
}

</style>



